import { FormGroup } from '@angular/forms';
import { EnderecoDTO } from './../models/domain/endereco.dto';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class ConsultaCEPService {

  constructor(private http: HttpClient) { }

  public consultarCEP(nrCep: string): Observable<EnderecoDTO>  {
    nrCep = nrCep.replace(/\D/g, '');
    if (nrCep != "") {
      var validaCep = /^[0-9]{8}$/;
      if (validaCep.test(nrCep)) {
        return this.http.get<EnderecoDTO>(`//viacep.com.br/ws/${nrCep}/json/`);
      }
    }
  }

  handerErro(err: any): Promise<any> {
    //console.log('Error:', err);
    return Promise.reject(err.message || err)
  }
  
  public preencherEndereco(form: FormGroup, end: EnderecoDTO) {
    form.patchValue({
        endereco: {
          id: [],
          cep: end.cep,
          bairro: end.bairro,
          complemento: end.complemento,
          logradouro: end.logradouro,
          uf: end.uf,
          cidade: end['localidade']
        }
      });
    return form;
  }
}


