import { ClienteService } from './../cliente.service';
import { EstadoDTO } from './../../../../../models/domain/estado.dto';
import { ConsultaCEPService } from './../../../../../services/consulta-cep-service';
import { EnderecoDTO } from './../../../../../models/domain/endereco.dto';
import { API_CONFIG } from 'config/core/api.config';
import { ClienteDTO } from './../../../../../models/domain/cliente.dto';
import { CategoriaDTO } from 'models/domain/categoria.dto';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';



@Component({
  selector: 'cliente-popup-modal',
  templateUrl: './cliente-popup-modal.component.html',
  styleUrls: ['./cliente-popup-modal.component.scss']
})
export class ClientePopupModalComponent extends AbstractPopupComponent implements OnInit {

  isErro: boolean =false;
  errorMensagem: string ="";

  constructor(
    public fb: FormBuilder,
    public clienteService: ClienteService,
    public consultaCEPService: ConsultaCEPService) {
      super();
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastro de cliente")
  }

  showAlteracao(item: ClienteDTO) {
    this.alterarForm(item)
    this.showPopup("Alterar cliente");
  }

  btnConfirmar(cliente: ClienteDTO) {
    cliente.urlImagem = this.urlFoto
    this.acao(cliente, this.clienteService);
  }

  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  consultarCpf(nrCpf: string) {
    this.clienteService.consultarPorCpf(nrCpf)
      .subscribe(res => { 
        this.isErro = true;
        this.errorMensagem ="O CPF informando já foi utilizado";
      }, error => { })
  }

  private alterarForm(cliente: ClienteDTO) {
    this.formulario.patchValue({
      id: cliente.id,
      nmPessoa: cliente.nmPessoa,
      nrCpf: cliente.nrCpf,
      dtNascimento: cliente.dtNascimento,
      status: cliente.status,
      urlImagem: cliente.urlImagem,
      emAlteracao: true,
      fone: {
        id: cliente.fone.id,
        nrFoneCelular: cliente.fone.nrFoneCelular,
        nrFoneResidencial: cliente.fone.nrFoneResidencial,
        nrFoneWhatsapp: cliente.fone.nrFoneWhatsapp,
      },
      endereco: {
        id: cliente.endereco.id,
        cep: cliente.endereco.cep,
        complemento: cliente.endereco.complemento,
        numero: cliente.endereco.numero,
        bairro: cliente.endereco.bairro,
        logradouro: cliente.endereco.logradouro,
        uf: cliente.endereco['cidade']['estado']['sgEstado'],
        cidade: cliente.endereco['cidade']['nome']
      }
    })
    this.urlFoto = cliente.urlImagem == undefined ? `${API_CONFIG.bucketBaseUrl}/clientes/cli${cliente.id}.jpg` : cliente.urlImagem;
  }

  private criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nmPessoa: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(120)]],
      nrCpf: ['', [Validators.required]],
      dtNascimento: [''],
      status: ['A'],
      urlImagem: [''],
      emAlteracao: [false],
      fone: this.fb.group({
        id: [''],
        nrFoneCelular: [''],
        nrFoneResidencial: [''],
        nrFoneWhatsapp: [''],
      }),
      endereco: this.fb.group({
        id: [],
        cep: [''],
        complemento: [''],
        numero: [''],
        bairro: [''],
        logradouro: [''],
        uf: [''],
        cidade: ['']
      }),

      usuario: this.fb.group({
        login: [''],
        email: [''],
        senha: [''],
        senhaConfirmacao: ['']
      })
    })
    this.urlFoto = this.urlClienteAvatar;

  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }

  public consultarCEP(nrCep: string) {
    this.consultaCEPService.consultarCEP(nrCep)
      .subscribe(res => { this.formulario = this.consultaCEPService.preencherEndereco(this.formulario, res) })
  }


}



