import { ClienteDTO } from './../../../../models/domain/cliente.dto';
import { Injectable } from  '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { API_CONFIG } from './../../../../config/core/api.config';
import { AbstractService } from 'config/core/abstract-service';

import { ProdutoDTO } from 'models/domain/produto.dto';
//import { catchError, retry } from 'rxjs/operators';


@Injectable()
export class ClienteService extends AbstractService {

  url: string = `${API_CONFIG.baseUrl}/clientes`;

  constructor(public http: HttpClient){
    super();
  }

  findById(id: string): Observable<{}> {
    return this.http.get(`${this.url}/${id}`,this.getHearderToken() );
  }

  findAll(): Observable<ClienteDTO[]> {
    return this.http.get<ClienteDTO[]>(this.url, this.getHearderToken() );
  }

  salvar(cliente: ClienteDTO) {
    return this.http.post(`${this.url}`, JSON.stringify(cliente), this.getHearderTokenNoResponse());
  }

  changeStatus(cliente: ClienteDTO) {
    return this.http.put(`${this.url}/${cliente.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
  }

  consultarPorCpf(nrCpf: string){
    return this.http.get<ClienteDTO>(`${this.url}?cpf=${nrCpf}`,this.getHearderToken() );
  }

  atualizar(cliente: ClienteDTO) {
    return this.http.put(`${this.url}/${cliente.id}`, JSON.stringify(cliente), this.getHearderTokenNoResponse());
  }

  excluir(cliente: ClienteDTO): Observable<{}> {
    return this.http.delete(`${this.url}/${cliente.id}`, this.getHearderToken());
  }

}



